"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";

export default function Footer() {
  const [email, setEmail] = useState("");

  function handleSubmit(e) {
    e.preventDefault();
  }

  return (
    <footer className="w-full bg-[#1E2933] text-white pt-16 pb-10">
      <div className="max-w-[1100px] mx-auto px-4">
        {/* Newsletter */}
        <div className="text-center">
          <h3 className="text-2xl md:text-3xl font-bold">Stay Updated</h3>
          <p className="mt-2 text-[#B7C1C8] text-sm md:text-base">
            Subscribe to our newsletter for the latest updates on inclusive hiring and talent
            acquisition.
          </p>

          <form onSubmit={handleSubmit} className="mt-5 flex justify-center">
            <div className="flex w-full max-w-[540px] overflow-hidden rounded-md">
              <input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                placeholder="Enter your email address"
                className="flex-1 h-11 px-4 text-sm text-[#0f1720] placeholder-[#7B8A93] outline-none"
              />
              <Button type="submit" className="h-11 rounded-none bg-[#cd7f32] hover:bg-[#cd7f32]/90 px-4 text-sm text-white">
                Subscribe
              </Button>
            </div>
          </form>

          <p className="mt-3 text-[12px] text-[#92A1AB]">
            We respect your privacy. Unsubscribe at any time.
          </p>
        </div>

        <div className="mt-10 h-px bg-[#2A3945]" />

        {/* Links + Social */}
        <div className="mt-6 grid grid-cols-1 md:grid-cols-4 gap-8 items-start">
          <div className="flex items-center gap-3">
            <div className="w-9 h-9 rounded-full border border-[#2A3945] flex items-center justify-center">
              <span className="text-[#E2E8F0] text-sm">ET</span>
            </div>
            <span className="text-[#E2E8F0]">Enabled Talent</span>
          </div>

          <div>
            <p className="text-[#B7C1C8] text-sm">Company</p>
            <ul className="mt-3 space-y-2 text-sm text-[#E2E8F0]">
              <li>Home</li>
              <li>About us</li>
              <li>Careers</li>
              <li>Contact Us</li>
            </ul>
          </div>

          <div>
            <p className="text-[#B7C1C8] text-sm">Services</p>
            <ul className="mt-3 space-y-2 text-sm text-[#E2E8F0]">
              <li>For Employers</li>
              <li>For Talents</li>
            </ul>
          </div>

          <div className="flex flex-col gap-4">
            <div>
              <p className="text-[#B7C1C8] text-sm">Legal</p>
              <ul className="mt-3 space-y-2 text-sm text-[#E2E8F0]">
                <li>Privacy Policy</li>
              </ul>
            </div>

            <div>
              <p className="text-[#B7C1C8] text-sm">Follow us</p>
              <div className="mt-3 flex items-center gap-3">
                {[
                  { label: "in" },
                  { label: "ig" },
                  { label: "fb" },
                  { label: "tt" },
                ].map((s, i) => (
                  <span
                    key={i}
                    className="w-9 h-9 rounded-full bg-[#2A3945] text-[#F3B774] flex items-center justify-center text-xs"
                  >
                    {s.label}
                  </span>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
}


